.card {
	flex: 1 1 0;

	display: block;
	padding: 10px;
	margin: 10px;
	border-radius: 0 0 5px 5px;
	min-width: 350px;
	max-width: 400px;

	transition: 500ms;
	box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, .1);
}

.card:hover {
	box-shadow: 15px 10px 2px 2px rgba(0, 0, 0, .1);
}

.card-title {
	display: block;
	background: hsla(0, 0%, 84%, .7);
	margin: -10px;
	margin-bottom: 10px;
	padding: 5px 10px;
	font-weight: bold;
}

.card-logo {
	max-height: 100px;
	float: left;
	margin: 0 10px 0 0;
}

.card-activity {
	font-size: 0.8em;
	color: gray;
}

.card-comment {
	font-size: 0.9em;
}